import { combineReducers } from 'redux'
import sidebarReducer from './sidebarReducer'
import pageTitleReducer from './pageTitleReducer'
import breadcrumbsReducer from './breadcrumbsReducer'
import pageLoadingReducer from './pageLoadingReducer'
import snackbarReducer from './snackbarReducer'
import pdfReducer from './pdfReducer'
import hospitalAffiliationReducer from './Partner/hospitalAffiliationReducer'
import affiliationTypeReducer from './affiliationType'
import AffiliationHospitalForm from './Partner/hospitalAffiliationFormReducer'
import AffiliationDoctorForm from './Partner/doctorAffiliationFormReducer'
import affiliatedContacts from './Partner/listOfAffiliatedContacts'
import affiliatedFiles from './Partner/affiliatedFiles'
import dentalClinicAffilFormReducer from './Partner/dentalClinicAffilFormReducer'
import medClinicAffiliationForm from './Partner/medClinicAffiliationFormReducer'
import pfMultiplierEditable from './Partner/pfMultiplerEditable'
import roomTypeEditable from './Partner/roomTypeEditable'
import facilityGroupingEditable from './Partner/facilityGroupingEditable'
import { hospitalFacilityDiscount } from './Partner/FacilityDiscountReducer'
import facilityDiscountToDelete from './Partner/FacilityDiscountToDeleteReducer'
import facilityDiscountButtonReducer from './Partner/facilityDiscountButtonReducer'
import autoSuggestData from './Partner/autoSuggestReducer'
import specialOtherService from './Partner/specialOtherReducer'
import addressReducer from './Partner/addressReducer'
import MaintenanceTableReducer from './Partner/MaintenanceTable'
import partnerProfileReducer from './Partner/partnerProfileReducer'

//* Partner OIC Reducers
import DentalReducer from './Partner/OIC/dental'
import HospitalReducer from './Partner/OIC/hospital'
import MedicalReducer from './Partner/OIC/medical'

const reducers = combineReducers({
  sidebar: sidebarReducer,
  pageTitle: pageTitleReducer,
  breadcrumbs: breadcrumbsReducer,
  pageLoading: pageLoadingReducer,
  snackbar: snackbarReducer,
  pdf: pdfReducer,
  hospital: hospitalAffiliationReducer,
  doctor: AffiliationDoctorForm,
  affiliationType: affiliationTypeReducer,
  AffiliationHospitalForm,
  affiliatedFiles: affiliatedFiles,
  AffiliationDoctorForm,
  affiliatedContacts,
  dentalClinicForm: dentalClinicAffilFormReducer,
  medClinicAffiliationForm,
  pfMultiplierEditable,
  roomTypeEditable,
  facilityGroupingEditable,
  hospitalFacilityDiscount,
  facilityDiscountToDelete,
  isButton: facilityDiscountButtonReducer,
  autoSuggestData,
  specialOtherService,
  addressReducer,
  MaintenanceTable: MaintenanceTableReducer,
  partnerProfileReducer,
  oicDental: DentalReducer,
  oicHospital: HospitalReducer,
  oicMedical: MedicalReducer
})

export default reducers
