
import React from 'react'
import Radio from '@material-ui/core/Radio'
import FormControlLabel from '@material-ui/core/FormControlLabel'

import classes from './ProcessType.module.scss'

//* standalone radio
const ProcessType = (props) => {
  const { state, setState } = props

  const handleOnChange = event => {
    const { target: { value = '' } } = event
    setState({ current: value })
  }

  // React.useEffect(() => {
  //   return () => {
  //     setState('None')
  //   }
  // }, [])
  return (
    <React.Fragment>
      <div className={classes.root}>
        <FormControlLabel value='Regular' control={<Radio checked={state === 'Regular'} size='small' color='primary' value='Regular' onChange={handleOnChange} />} label='Regular' />
        <FormControlLabel value='Umbrella' control={<Radio checked={state === 'Umbrella'} size='small' color='primary' value='Umbrella' onChange={handleOnChange} />} label='Umbrella' />
      </div>
    </React.Fragment>
  )
}

export default ProcessType
