import { DEVIATION_EDIT_BUTTON } from '../../actions/Partner/partnerProfile'

const initialState = {
  isEdit: false
}

export default function partnerProfileReducer(state = initialState, { type, payload }) {
  switch (type) {
    case DEVIATION_EDIT_BUTTON:
      return {
        ...state,
        isEdit: payload
      }

    default:
      return state
  }
}
