import { DialogContent, DialogActions, Dialog, Button } from '@material-ui/core'
import { withStyles } from '@material-ui/styles'

export const StyledDialog = withStyles(() => ({
  paper: {
    padding: 8,
  },
}))(Dialog)

export const StyledDialogContent = withStyles(() => ({
  root: {
    minWidth: 320,
  },
}))(DialogContent)

export const StyledDialogAction = withStyles(() => ({
  root: {
    justifyContent: 'center',
    marginBottom: 16,
    marginTop: 8,
  },
}))(DialogActions)

export const StyledButton = withStyles(() => ({
  root: {
    backgroundColor: '#3ab77d',

    '&:hover': {
      backgroundColor: '#3ab77d',
    },
  },
}))(Button)
