import types from '../Types/Types'
import duplicateValidator from '../../Utilities/duplicateValidation'
import sortFunction, { sortWithoutValueFunction } from '../../Utilities/sort'
import extractData from '../../Utilities/extractData'
import DeepClone from 'Utilities/Partner/DeepClone'
import requiredValidation from '../../Utilities/requiredValidator'

const initialData = {
  data: [],
  newData: [],
  extractedData: [],
  requiredData: [],
  duplicateData: [],
  readOnly: true,
  isSubmit: false,
  isCancel: false,
  isToAddRow: false,
  dataStructure: {},
  page: 1,
  count: 1,
  limit: 10,
  total: 1,
  lastPage: 1,
  hasDuplicate: false,
  showSaveAlert: false,
  showSavePageAlert: false,
  validateSave: false,
  validation: {
    rowDuplicate: { enable: true, checkFields: [] },
    requiredField: { enable: false, checkFields: [] }
  },
  filter: {},
  sort: {},
  apiSort: {
    sort: 'id',
    sortBy: true
  },
  filterSchema: {},
  fromUploaded: false,
  onClickUpload: false,
  affiliatedProcessType: {
    raw: '',
    current: ''
  }
}

const tableReducer = (state = initialData, action) => {
  let dataHandler = []
  let extractedData = []
  let requiredData = []
  let duplicateData = []
  switch (action.type) {
    case types.SET_INITIAL_VALUE:
      return { ...state, ...action.payload }
    case types.SET_READ_MODE:
      dataHandler = DeepClone(state.newData)
      if (action.payload === true) {
        dataHandler = DeepClone(state.data)
      }
      return { ...state, ...{ readOnly: action.payload, newData: [...dataHandler] } }
    case types.SET_EDIT_MODE:
      return { ...state, readOnly: action.payload }
    case types.SET_CANCEL:
      return { ...state, ...{ isCancel: action.payload } }
    case types.SET_SUBMIT:
      return { ...state, isSubmit: action.payload }
    case types.RESET_DATA:
      return { ...state, newData: DeepClone(state.data) }
    case types.UPDATE_DATA:
      dataHandler = DeepClone(state.newData)
      const { index, key, value } = action.payload
      if ('relation' in state.dataStructure[key] && value) {
        const findRelation = state.dataStructure[key].entries.find(obj => obj[state.dataStructure[key].key] === value)
        if (findRelation) {
          dataHandler[index][state.dataStructure[key].relation] = findRelation[state.dataStructure[key].target]
        }
      }
      dataHandler[index][key] = value
      return { ...state, newData: [...dataHandler] }
    case types.ADD_ROW:
      dataHandler = DeepClone(state.newData)
      const dataObj = {}
      for (let key in state.dataStructure) {
        if (state.dataStructure[key].type === 'select') {
          dataObj[key] = state.dataStructure[key].defaultMenu
        } else {
          dataObj[key] = ''
        }
      }
      dataHandler.push(dataObj)
      duplicateData = duplicateValidator(dataHandler, state.validation.rowDuplicate.checkFields)
      if (!state.validation.rowDuplicate.enable && duplicateData.length > 0) {
        return {
          ...state,
          ...{ isToAddRow: false, hasDuplicate: true, validateSave: false }
        }
      }
      return { ...state, ...{ newData: dataHandler, isToAddRow: false, hasDuplicate: false } }
    case types.DELETE_ROW:
      dataHandler = DeepClone(state.newData)
      dataHandler[action.payload] = { ...dataHandler[action.payload], deleted: true }
      return { ...state, newData: dataHandler }
    case types.SET_TABLE:
      extractedData = extractData(state.data, state.newData)
      if (!state.readOnly || (!state.readOnly && extractedData.length)) {
        return { ...state, showSavePageAlert: true }
      }
      if (
        !state.readOnly &&
        state.newData.length > 0 &&
        state.newData.filter(obj => !('_id' in obj) && !('deleted' in obj)).length > 0
      ) {
        return { ...state, showSavePageAlert: true }
      }
      return { ...state, ...action.payload }
    case types.VALIDATE_SAVE:
      dataHandler = DeepClone(state.newData)
      extractedData = extractData(state.data, state.newData)
      requiredData = requiredValidation(dataHandler, state.validation.requiredField.checkFields)
      duplicateData = duplicateValidator(dataHandler, state.validation.rowDuplicate.checkFields)
      if (state.fromUploaded) {
        const newData = dataHandler.map(standardRate => {
          delete standardRate._id
          return standardRate
        })
        extractedData = newData
      }
      if (!state.validation.rowDuplicate.enable && duplicateData.length > 0) {
        return {
          ...state,
          ...{ isToAddRow: false, hasDuplicate: true, validateSave: false }
        }
      }

      if (state.validation.requiredField.enable && !requiredData.valid) {
        return {
          ...state,
          ...{ isToAddRow: false, requiredData: requiredData.data, validateSave: false, isSubmit: false }
        }
      }

      return { ...state, ...{ validateSave: action.payload, extractedData: extractedData } }
    case types.UPDATE_FILTER:
      return { ...state, filter: { ...state.filter, [action.payload.key]: action.payload.value }, page: 1 }
    case types.SET_SORT:
      const sortedData = sortWithoutValueFunction(action.payload.key, state.newData, action.payload.value)
      return {
        ...state,
        ...{ newData: sortedData, sort: { ...state.sort, [action.payload.key]: action.payload.value } }
      }
    case types.SET_API_SORT:
      const { sort, sortBy } = action.payload
      const apiSort = { ...state.apiSort }
      apiSort.sort = sort
      apiSort.sortBy = sortBy
      return { ...state, apiSort }
    case types.ON_CLICK_UPLOAD:
      return { ...state, onClickUpload: action.payload }
    case types.IS_TO_ADD_ROW:
      return { ...state, isToAddRow: true }
    case types.SET_HAS_DUPLICATE:
      return { ...state, hasDuplicate: action.payload }
    case types.SHOW_SAVE_ALERT:
      return { ...state, showSaveAlert: action.payload }
    case types.SHOW_SAVE_PAGE_ALERT:
      return { ...state, showSavePageAlert: action.payload }
    case types.SET_UPLOAD:
      return { ...state, fromUploaded: action.payload }
    case types.SET_PAGE:
      return { ...state, page: action.payload }
    case types.AFFILIATED_PROCESS_TYPE:
      return { ...state, affiliatedProcessType: { ...state.affiliatedProcessType, ...action.payload } }
    //* FOR CPTS only
    case types.ENTRIES_CPTS:
      const entries = action.payload
      const dataStructure = state.dataStructure
      dataStructure.cpt.entries = entries
      dataStructure.procedure.entries = entries

      return { ...state, dataStructure: dataStructure }
    default:
      return { ...state }
  }
}

export default tableReducer
