import React from 'react'
import { Link } from 'react-router-dom'
import { Scrollbars } from 'react-custom-scrollbars'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useSelector, useDispatch } from 'react-redux'
import { setSidebarItems } from 'Pages/Protected/Store/actions'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import { withStyles, createMuiTheme } from '@material-ui/core/styles'
import { ThemeProvider } from '@material-ui/styles'
import { Provider } from 'react-redux'
// CSS Classes
import classes from './SideBarComponent.module.scss'
import SpecialOtherServices from '../../ProfileMaintainer/SpecialOtherServicesProfile2/Pages/SpecialOtherServices/SpecialOtherServices'
import HospitalList from '../../ProfileMaintainer/HospitalProfile/Pages/HospitalList/HospitalList'
// import DoctorList from '../../ProfileMaintainer/DoctorProfile/Pages/DoctorList/DoctorList'
import MedicalClinicList from '../../ProfileMaintainer/MedicalClinicProfile/Pages/MedicalClinicList/MedicalClinicList'
import DentalClinicList from '../../ProfileMaintainer/DentalClinicProfile/Pages/DentalClinicList/DentalClinicList'
import DentistList from '../../ProfileMaintainer/MedicalDentistProfile/Pages/DentistList/DentistList'
import storeOtherService from '../../ProfileMaintainer/SpecialOtherServicesProfile2/Store'
import GlobalFonts from 'Fonts/Fonts'
// import 'typeface-nunito'
import FacilityList from '../../ProfileMaintainer/Components/ProfileListView/FacilityList'
import DoctorListView from '../../ProfileMaintainer/Components/ProfileListView/DoctorListView'
import DoctorList from '../../ProfileMaintainer/Components/ProfileListView/DoctorList'

let pdispatch = null

const theme = createMuiTheme({
  typography: {
    fontFamily: ['NunitoSans'].join(',')
  }
})

const PartnerSideBarComponent = () => {
  pdispatch = useDispatch()
  const items = useSelector(state => state.sidebar)

  const [anchorEl, setAnchorEl] = React.useState(null)
  const [anchrEl, setAnchrEl] = React.useState(null)
  const [endPoint, setEndPoint] = React.useState('')

  const [openSpecialOtherServices, setopenSpecialOtherServices] = React.useState(false)
  const [openHospitalList, setopenHospitalList] = React.useState(false)
  const [openMedicalClinicList, setopenMedicalClinicList] = React.useState(false)
  const [openDoctorList, setOpenDoctorList] = React.useState(false)
  const [openDentistList, setOpenDentistList] = React.useState(false)
  const [openDentalClinicList, setopenDentalClinicList] = React.useState(false)

  const handleClick = event => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const handleClickItem = event => {
    setAnchrEl(event.currentTarget)
  }

  const handleCloseItem = () => {
    setAnchrEl(null)
  }

  const handleClickSOS = (endpoint, open) => {
    setopenSpecialOtherServices(open)
    setEndPoint(endpoint)
    setAnchorEl(null)
  }

  const handleClickMenuHospital = (endpoint, open) => {
    setopenHospitalList(open)
    setEndPoint(endpoint)
    setAnchorEl(null)
  }

  const handleClickDoctorList = (endpoint, open) => {
    setOpenDoctorList(open)
    setEndPoint(endpoint)
    setAnchorEl(null)
  }

  const handleClickDentistList = (endpoint, open) => {
    setOpenDentistList(open)
    setEndPoint(endpoint)
    setAnchorEl(null)
  }

  const handleClickMenuMedicalClinic = (endpoint, open) => {
    setopenMedicalClinicList(open)
    setEndPoint(endpoint)
    setAnchorEl(null)
  }

  const handleClickMenuDentalClinic = (endpoint, open) => {
    setopenDentalClinicList(open)
    setEndPoint(endpoint)
    setAnchorEl(null)
  }

  const StyledMenu = withStyles({
    paper: {
      backgroundColor: 'rgba(30, 32, 113,0.5)',
      borderRadius: 0
    }
  })(props => (
    <Menu
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
      style={{
        marginLeft: '62px',
        marginTop: '-60px'
      }}
      {...props}
    />
  ))

  const StyledMenuItem = withStyles({
    paper: {
      backgroundColor: 'rgba(30, 32, 113,0.5)'
    }
  })(props => (
    <MenuItem
      style={{
        color: '#ffffff',
        fontSize: '14px',
        textDecoration: 'none'
      }}
      {...props}
    />
  ))

  const StyledMenuTable = withStyles({
    paper: {
      backgroundColor: 'rgba(30, 32, 113,0.5)',
      borderRadius: 0
    }
  })(props => (
    <Menu
      anchrEl={anchrEl}
      keepMounted
      open={Boolean(anchrEl)}
      onClose={handleCloseItem}
      style={{
        marginLeft: '62px',
        marginTop: '2em'
      }}
      {...props}
    />
  ))

  const StyledMenuItemTable = withStyles({
    paper: {
      backgroundColor: 'rgba(30, 32, 113,0.5)'
    }
  })(props => (
    <MenuItem
      style={{
        color: '#ffffff',
        fontSize: '14px'
      }}
      {...props}
    />
  ))

  const renderSidebarWithSubMenu = item => {
    switch (item.field) {
      case 'partners':
        return <Link to='#' className={classes.item}>
          <div
            className={classes.circle}
            aria-controls='medical-partner-profile-maintainer-menu'
            aria-haspopup='true'
            onClick={handleClick}
          >
            <FontAwesomeIcon icon='users' className={classes.icon} />
          </div>
          <div>
            <StyledMenu
              id='medical-partner-profile-maintainer-menu'
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <StyledMenuItem onClick={() => handleClickMenuHospital('/medicalHospitalList', true)}>
                Hospital
              </StyledMenuItem>
              <StyledMenuItem onClick={() => handleClickMenuMedicalClinic('/medicalClinicList', true)}>
                Medical Clinic
              </StyledMenuItem>
              <StyledMenuItem onClick={() => handleClickDoctorList('/medicalDoctorList', true)}>
                Medical Doctor
              </StyledMenuItem>
              <StyledMenuItem onClick={() => handleClickMenuDentalClinic('/dentalClinicList', true)}>
                Dental Clinic
              </StyledMenuItem>
              <StyledMenuItem onClick={() => handleClickDentistList('/medicalDentist', true)}>
                Medical Dentist
              </StyledMenuItem>
              <StyledMenuItem onClick={() => handleClickSOS('/SpecialOrOtherService', true)}>
                Special Services
              </StyledMenuItem>
            </StyledMenu>
          </div>
          <span className={classes.text}>{item.label}</span>
        </Link>
      case 'maintenance_table':
        return <Link to='#' className={classes.item}>
          <div
            className={classes.circle}
            aria-controls='medical-partner-table-maintainer-menu'
            aria-haspopup='true'
            onClick={handleClickItem}
          >
            <FontAwesomeIcon icon='calendar-alt' className={classes.icon} />
          </div>
          <div>
            <StyledMenuTable
              id='medical-partner-table-maintainer-menu'
              anchrEl={anchrEl}
              keepMounted
              open={Boolean(anchrEl)}
              onClose={handleCloseItem}
            >
              <a
                href='#'
                style={{ textDecoration: 'none' }}
                onClick={() => {
                  window.location = './../system-admin/index.html#/system-admin/lookup'
                }}
              >
                <StyledMenuItemTable>Code Management</StyledMenuItemTable>
              </a>

              <Link
                to='/FacilityGroupingMaintenance'
                onClick={() => handleCloseItem()}
                style={{ textDecoration: 'none' }}
              >
                <StyledMenuItemTable>Facility Grouping</StyledMenuItemTable>
              </Link>
              <Link to='/RoomTypeMaintenance' onClick={() => handleCloseItem()} style={{ textDecoration: 'none' }}>
                <StyledMenuItemTable>Room Types</StyledMenuItemTable>
              </Link>
              <Link to='/PFMultiplier' onClick={() => handleCloseItem()} style={{ textDecoration: 'none' }}>
                <StyledMenuItemTable>PF Multiplier</StyledMenuItemTable>
              </Link>
            </StyledMenuTable>
          </div>
          <span className={classes.text}>{item.label}</span>
        </Link>

      default:
        return null
    }
  }

  const renderSideBarItems = item => {
    if (!item.type || (item.type && item.type) === 'link') {
      return (
        <Link to={item.to} className={classes.item} key={item.to}>
          <div className={classes.circle}>
            <FontAwesomeIcon icon={item.icon} className={classes.icon} />
          </div>
          {item.label ? <span className={classes.text}>{item.label}</span> : ''}
        </Link>
        // <Link id={`${item.label}`} to={item.to} className={classes.item} key={item.to}>
        //   <div className={classes.circle}>
        //     <FontAwesomeIcon icon={item.icon} className={classes.icon} />
        //   </div>
        //   {item.label ? <span className={classes.text}>{item.label}</span> : ''}
        // </Link>

      )
    } else {
      return (
        <React.Fragment key={'pop-up-group'}>
          {renderSidebarWithSubMenu(item)}
        </React.Fragment>
      )
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <GlobalFonts />
        <Scrollbars className={classes.menu}>
          {items.map(item => renderSideBarItems(item))}
        </Scrollbars>

        {openSpecialOtherServices && (
          <Provider store={storeOtherService}>
            <SpecialOtherServices
              open={openSpecialOtherServices}
              setOpen={setopenSpecialOtherServices}
              endPoint={endPoint}
            />
          </Provider>
        )}

        {/* {openHospitalList && <HospitalList open={openHospitalList} setOpen={setopenHospitalList} endPoint={endPoint} />} */}
        {openHospitalList && (
          <FacilityList
            open={openHospitalList}
            setOpen={setopenHospitalList}
            endPoint={endPoint}
            paginationText='Hospital Partners'
            url='/HospitalProfile/PartnerInformation'
            type='HOSPITAL'
          />
        )}

        {/* {openDoctorList && <DoctorList open={openDoctorList} setOpen={setOpenDoctorList} endPoint={endPoint} />} */}
        {openDoctorList && (
          <DoctorList
            open={openDoctorList}
            setOpen={setOpenDoctorList}
            endPoint={endPoint}
            paginationText='Medical Doctor Partners'
            url='/DoctorProfile/PartnerInformation'
            type='DOCTOR'
          />
          // <DoctorListView
          //   open={openDoctorList}
          //   setOpen={setOpenDoctorList}
          //   endPoint={endPoint}
          //   paginationText='Medical Doctor Partners'
          //   url='/DoctorProfile/PartnerInformation'
          //   type='DOCTOR'
          // />
        )}
        {/* {openDentistList && <DentistList open={openDentistList} setOpen={setOpenDentistList} endPoint={endPoint} />} */}
        {openDentistList && (
          <DoctorList
            open={openDentistList}
            setOpen={setOpenDentistList}
            endPoint={endPoint}
            paginationText='Medical Dentist Partners'
            url='/MedicalDentist/PartnerInformation'
            type='DENTIST'
          />
          // <DoctorListView
          //   open={openDentistList}
          //   setOpen={setOpenDentistList}
          //   endPoint={endPoint}
          //   paginationText='Medical Dentist Partners'
          //   url='/MedicalDentist/PartnerInformation'
          //   type='DENTIST'
          // />
        )}
        {openMedicalClinicList && (
          <FacilityList
            open={openMedicalClinicList}
            setOpen={setopenMedicalClinicList}
            endPoint={endPoint}
            paginationText='Medical Clinic Partners'
            url='/MedicalClinicProfile/PartnerInformation'
            type='CLINIC'
          />
        )}

        {openDentalClinicList && (
          <FacilityList
            open={openDentalClinicList}
            setOpen={setopenDentalClinicList}
            endPoint={endPoint}
            paginationText='Dental Clinic Partners'
            url='/DentalClinicProfile/PartnerInformation'
            type='DENTAL_CLINIC'
          />
        )}
      </div>
    </ThemeProvider>
  )
}

const PartnerSideBarService = {
  setItems: items => {
    pdispatch(setSidebarItems(items))
  }
}

export { PartnerSideBarComponent, PartnerSideBarService }
