import React from 'react'
import ReactTable from '../../../Helpers/Pagination'
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'
import classes from './DentalClinicList.module.scss'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import { medicalClinicListCol } from '../../../../../../../Constant/tableColumnData.json'

const DentalClinicList = ({ open, setOpen, endPoint }) => {
  const [tableData, setTableData] = React.useState([])
  const [tableColumn] = React.useState(medicalClinicListCol)
  const [loading, setLoading] = React.useState(true)

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchData()
    }

    return () => (mounted = false)
  }, [])

  const fetchData = async () => {
    setLoading(true)
    try {
      const {
        data: { data: res }
      } = await ProfileService.getClinicByType('DENTAL_CLINIC')
      let arr = []
      setLoading(false)
      Object.keys(res).forEach(items => {
        let obj = {}
        obj.medicalPartnerId = (res[items] || {}).id || ''
        obj.facilityName = (res[items] || {}).name || ''
        obj.Status = (res[items] || {}).status || ''
        obj.location = ((res[items] || {}).address || {}).city || ''
        arr.push(obj)
      })
      setTableData(arr)
    } catch (e) {
      console.log('error', e)
    }
  }

  const HandleCloseModal = data => {
    setOpen(false)
  }

  const memoizedData = React.useMemo(() => tableData, [tableData])
  const memoizedcolumn = React.useMemo(() => tableColumn, [tableColumn])

  return (
    <React.Fragment>
      <Modal
        open={open}
        onClose={() => {
          setOpen(false)
        }}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div>
              <ReactTable
                columns={memoizedcolumn}
                data={memoizedData}
                countName={'Dental Clinic list'}
                CloseModal={HandleCloseModal}
                link={'/DentalClinicProfile/PartnerInformation'}
                type={'clinic'}
                loading={loading}
              />
            </div>
            <div id='Close-button'>
              <Button
                className={classes.closeButton}
                variant='contained'
                onClick={() => {
                  setOpen(false)
                }}
              >
                Close
              </Button>
            </div>
          </div>
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default DentalClinicList
