import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import InputComponent from '../../../InputComponent/InputComponent'
import {
  Paper,
  InputBase
} from '@material-ui/core'

//* Process Type component for affiliated doctors/facilities
import ProcessTypeComponent from 'Pages/Protected/Partner/ProfileMaintainer/Components/ListOfAffiliatedTables/ProcessType/ProcessType'

import classes from './TableHeader.module.scss'
import FacilityGroupingService from 'Services/Api/RoomType/RoomType'

const TableHeader = props => {
  const {
    enableSort = true,
    field = {},
    loading = false,
    options = [],
    inputValue = '',
    customStyle = {},
    value = null,
    keyProp = '',
    type = '',
    filter = '',
    onChangeText = '',
    sort = {},
    setSort = '',
    width = '',
    readOnly = true,
    //* process_type
    processType = '',
    onChangeProcessType = () => { },
    hasCustomComponent = false,
    menu = [],
    defaultMenu = ''
  } = props

  const buildInputComponent = (type, field, filter, onChangeText, readOnly) => {
    if (!readOnly) {
      return <Paper style={{ display: 'flex', width: '100%', alignItems: 'center', padding: '0 10px', background: '#d3d3d3' }}>
        <InputBase
          value={filter[field]}
          style={{ width: '100%' }}
          type='text'
          disabled={!readOnly}

        />
      </Paper>
    }
    return (
      <InputComponent
        style={{ width: '100%' }}
        type={type}
        isHeader
        value={filter[field]}
        name={field}
        onChangeText={onChangeText}
        menu={menu}
        defaultMenu={defaultMenu}
      />
    )
  }

  return (
    <TableCell
      key={`partnerHeaderCell_${keyProp}`}
      style={{ verticalAlign: 'top', ...customStyle }}
      className={classes.tableHeaderCell}
    >
      <div className={classes.headerFields}>
        <span className={classes.tableHeaderTitle}>{props.children}</span>
        {enableSort && readOnly && (
          <FontAwesomeIcon
            icon='sort'
            className={classes.tableHeaderSortIcon}
            onClick={() => {
              setSort(field, !sort[field],)
            }}
          />
        )}
      </div>
      {type && buildInputComponent(type, field, filter, onChangeText, readOnly)}
      {hasCustomComponent && field === 'process_type' && <ProcessTypeComponent state={processType} setState={onChangeProcessType} />}

    </TableCell>
  )
}

export default TableHeader
