import React, { useState } from 'react'

import IdleTimer from 'react-idle-timer'
import { Typography } from '@material-ui/core'
import { StyledButton, StyledDialog, StyledDialogAction, StyledDialogContent } from './idle.styles'

const Idle = ({ logout }) => {
  const [showIdleModal, setShowIdleModal] = useState(false)

  const handleLogout = async () => {
    setShowIdleModal(false)
    await logout()
  }

  const handleOnIdle = () => {
    setShowIdleModal(true)
  }

  return (
    <>
      <IdleTimer timeout={1000 * 60 * 15} onIdle={handleOnIdle} debounce={250} />
      {showIdleModal && (
        <StyledDialog open onClose={handleLogout}>
          <StyledDialogContent>
            <Typography>You have been logged out due to inactivity</Typography>
          </StyledDialogContent>
          <StyledDialogAction>
            <StyledButton variant="contained" color="primary" onClick={handleLogout}>
              Okay
            </StyledButton>
          </StyledDialogAction>
        </StyledDialog>
      )}
    </>
  )
}

export default Idle
