const initialState = {
  hospitalFacilityDiscount: [],
  errors: {},
  currentItem: {},
  mockForm: {
    status: '',
    discount_type: '',
    claim_type: '',
    discount_application: '',
    included_procedure: [],
    payment_type: '',
    terms_in_days: '',
    rate: '',
    effectivity_date: null,
    end_date: null,
    discount_to_be_deducted_from: '',
    other_details_of_discount: '',
  }
}

export function hospitalFacilityDiscount (state = initialState, { type, payload }) {
  switch (type) {
    case 'SET_HOSPITAL_FACILITY_DISCOUNT':
      return {
        ...state,
        hospitalFacilityDiscount: payload
      }

    case 'SET_ERRORS':

      return { ...state, errors: payload }

    case 'SET_CURRENT_ITEM':
      return { ...state, currentItem: payload }

    default:
      return state
  }
}
