export function setHospitalFacilityDiscount (payload) {
  return {
    type: 'SET_HOSPITAL_FACILITY_DISCOUNT',
    payload
  }
}

export function setFacilityErrors (payload) {
  return {
    type: 'SET_ERRORS',
    payload
  }
}

export function setCurrentItem (payload) {
  return {
    type: 'SET_CURRENT_ITEM',
    payload
  }
}
